<template>
  <div>
    <b-collapse id="formCollapse">
      <b-form @submit.prevent="processForm">
        <h4 class="mt-4 mb-3">User information</h4>
        <b-row class="mb-3">
          <b-col>
            <label for="username" class="form-label">Username</label>
            <b-form-input :disabled="editState" v-model="username" id="username"></b-form-input>
          </b-col>
          <b-col>
            <label for="email" class="form-label">Email</label>
            <b-form-input :disabled="editState" v-model="email" id="email"></b-form-input>
          </b-col>
          <b-col cols="3">
            <label for="type"  class="form-label">Rol</label>
            <b-form-select
              v-model="rol"
              :options="$constants().optionsRolType"
            >
            </b-form-select>
          </b-col>
          <b-col cols="3">
            <label for="password" class="form-label">Password</label>
            <b-form-input
              :disabled="editState"
              v-model="password"
              type="password"
              id="password"
            ></b-form-input>
          </b-col>
        </b-row>
        <b-row class="mb-3">
          <b-col>
            <label for="sites" class="form-label">Sites</label>
            <multiselect
              v-model="sitesSelected"
              :options="sites"
              :multiple="true"
              :close-on-select="false"
              placeholder="Select options">
            </multiselect>
          </b-col>
        </b-row>
        <div class="d-grid gap-2 d-md-flex justify-content-md-end">
          <b-button
            variant="outline-danger"
            class="actionButton"
            v-b-toggle:formCollapse
            @click="reset()"
            ><i class="far fa-times-circle mr-1"></i>Cancel</b-button
          >
          <b-button
            variant="outline-success"
            type="submit"
            class="actionButton"
            v-b-toggle:formCollapse
            ><i class="far fa-check-circle mr-1"></i
            >{{ txtBtnFormulario }}</b-button
          >
        </div>
      </b-form>
    </b-collapse>
    
  </div>
</template>

<script>

export default {
  name: 'User',
  data() {
    return {
      // User fields
      id: '',
      username: '',
      email: '',
      password: '',
      roles: [],
      rol:'',
      value: null,
      //-----------
      perPage: 10,
      currentPage: 1,
      txtBtnFormulario: 'Save',
      editState: false,
      sites:[],
      sitesSelected:[]
    };
  },
  methods: {
    getData() {
      this.$parent
        .getDataFromType('site')
        .then(data => {
          for(var i = 0; i < data.length; i++)
          {
            this.sites.push(data[i].name)
          }
         
        })
        .catch(error => this.$parent.catchError(error));
    },
    processForm() {

      var attributes = [];
      for(var i = 0;i < this.sitesSelected.length;i++)
      {
        attributes.push({"code":"site","value":this.sitesSelected[i]});
      }

      this.roles.push(this.rol);

      if (this.editState == false) {
        var dataSave = {
          username: this.username,
          email: this.email,
          password: this.password,
          role: this.roles,
          attributes: attributes
        };

        this.$parent.saveData(dataSave);
      } else {
        var dataUpdate = {
          id: this.id,
          username: this.username,
          email: this.email,
          password: this.password,
          role: this.roles,
          attributes: attributes
        };

        this.$parent.updateData(dataUpdate);
      }
      console.log("SitesSelected"+this.sitesSelected);
      this.reset();
    },
    reset() {
      this.id = '';
      this.username = '';
      this.email = '';
      this.password = '';
      this.roles = [];
      this.rol = '';
      this.sitesSelected = [];

      this.txtBtnFormulario = 'Save';
      this.editState = false;
    },
    loadItem(item) {
      this.editState = true;
      this.id = item.id;
      this.username = item.username;
      this.email = item.email;
      this.password = '';

      if(item.roles[0].name == 'ROLE_INSPECTOR'){this.rol = 'ROLE_INSPECTOR';}
      if(item.roles[0].name == 'ROLE_ADMIN'){this.rol = 'ROLE_ADMIN';}

      for(var i=0;i<item.attributes.length;i++)
      {
        if(item.attributes[i].code == 'site') {this.sitesSelected.push(item.attributes[i].value)}
      }
      this.txtBtnFormulario = 'Update';
      
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    }
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push('/login');
    }
    this.getData();
  },
  components: {}
};
</script>
<style></style>